import { Component, OnInit, OnDestroy, ViewChild, ChangeDetectorRef, ViewEncapsulation, ElementRef } from '@angular/core';
import { MatSidenav } from '@angular/material';
import { MediaMatcher } from '@angular/cdk/layout';
import { Router } from '@angular/router';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css'],
  encapsulation: ViewEncapsulation.None,
})
export class AppComponent implements OnInit, OnDestroy {
  title = 'aaa2019';

  @ViewChild('sidenav') sidenav: MatSidenav;
  mobileQuery: MediaQueryList;
  private sidenavOpened = false;

  constructor(private changeDetectorRef: ChangeDetectorRef, media: MediaMatcher,
    private router: Router, private _elementRef : ElementRef) {
    /* Detectar los cambios para controlar la navegacion lateral */
    this.mobileQuery = media.matchMedia('(max-width: 600px)');
    const mobileQueryListener = () => changeDetectorRef.detectChanges();
    this.mobileQuery.addListener(mobileQueryListener);
  }


  openMenu(){
    this.sidenav.toggle();
  }

  ngOnInit(){
    this.sidenav.openedChange.subscribe(() => {
      if (this.sidenav.opened && this.sidenav.mode === 'over') {
         document.body.style.cssText = 'overflow: hidden';
      } else {
        document.body.style.cssText = 'overflow: auto';
      }
    });
  }

  ngOnDestroy(): void {
    this.changeDetectorRef.detach();
  }


}
